import styled, { css } from "styled-components";

const genreList = {
  28: "",
  12: "",
  16: "",
  35: "",
  80: "",
  99: "",
  18: "",
  10751: "",
  14: "",
  36: "",
  27: "",
  10402: "",
  9648: "",
  10749: "",
  878: "SF",
  10770: "TV",
  53: "",
  10752: "",
  37: "",
};

function Filter({
  genreIndexs,
  activeGenreIndex,
  setActiveGenreIndex,
  allMoviesIndex,
}) {
  return (
    <Wrapper>
      <Button
        active={activeGenreIndex === allMoviesIndex}
        onClick={() => setActiveGenreIndex(allMoviesIndex)}
      >
        All
      </Button>
      {genreIndexs?.map((genreIndex) => (
        <Button
          key={genreIndex}
          active={activeGenreIndex === genreIndex}
          onClick={() => setActiveGenreIndex(genreIndex)}
        >
          {genreList[genreIndex]}
        </Button>
      ))}
    </Wrapper>
  );
}

export default Filter;

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 4rem;
`;

const Button = styled.button.attrs({ type: "button" })`
  min-width: 5rem;
  padding: 0.5rem 1rem;
  margin-bottom: 0.5rem;
  background-color: white;
  border-radius: 1rem;
  border: 1px solid lightgray;
  font-weight: bold;
  color: black;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 1rem;
  }

  ${(props) =>
    props.active &&
    css`
      background-color: black;
      border-color: black;
      color: white;
    `}
`;
